// src/components/AverageCard.tsx

import { getMood, MoodValue } from "@/app/lib/moods";
import { config } from "./config";

type Props = {
  type: "mood" | "sleep";
  value?: MoodValue | null;
};

function isValueEmpty(value: MoodValue | null | undefined) {
  return value === null || value === undefined;
}

export default function AverageCard({ type, value }: Props) {
  const empty = isValueEmpty(value);
  const isMoodType = type === "mood";
  const mood = isMoodType && !empty ? getMood(value as MoodValue) : null;
  const currentConfig = isMoodType ? config.mood : config.sleep;

  const backgroundColor = !empty
    ? isMoodType
      ? mood!.color
      : "bg-blue-600"
    : "bg-blue-100";

  const iconSrc = !empty
    ? isMoodType
      ? mood!.icon.color
      : "bg-blue-600"
    : "bg-blue-100";

  return (
    <div className="flex flex-col gap-150">
      <p className="preset-5 text-neutral-900">
        {currentConfig.title}
        <span className="preset-7 text-neutral-600"> (Last 5 Check-ins)</span>
      </p>
      <div
        className={`${backgroundColor} rounded-16 px-200 py-300 flex flex-col gap-150 relative overflow-hidden`}
      >
        {/* Decorative background image */}
        <img
          src="/images/bg-pattern-averages.svg"
          alt=""
          aria-hidden="true"
          className="absolute right-0 top-1/2 -translate-y-1/2 size-[200%] translate-x-5/8 pointer-events-none select-none"
          style={{ zIndex: 0 }}
        />
        <div className="relative z-10">
          {!empty ? (
            <>
            <img src={iconSrc} alt="" aria-hidden="true" className="size-100" />
              <p className="preset-1 font-bold text-neutral-900 mb-2">{value}</p>
              <p className="preset-7 text-neutral-600">{currentConfig.emptySubtitle}</p>
            </>
          ) : (
            <>
              <p className="preset-4 font-semibold text-neutral-900">{currentConfig.title}</p>
            </>
          )}
        </div>
      </div>
    </div>
  );
}
  